import $ from 'jquery';
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { connect } from 'react-redux';
import Intercom from 'react-intercom';

class IntercomMain extends Component {
  constructor(props) {
    super(props);

    this.state = {
      intercom: null,
      visibleState: true,
    };
  }

  componentDidMount() {
    this.interval = setInterval(() => {
      const { intercom, visibleState } = this.state;
      const { isVisible } = this.props;

      if (visibleState !== isVisible) {
        const intercomToUse = intercom || ($('#intercom-container').length > 0 ? $('#intercom-container') : null);
        if (intercomToUse) {
          if (isVisible) {
            intercomToUse.show();
          } else {
            intercomToUse.hide();
          }
        }

        this.setState({
          visibleState: isVisible,
        });
      }
    }, 300);
  }

  componentWillUnmount() {
    clearInterval(this.interval);
  }

  render() {
    return (
      <Intercom
        appID="yltolfa1"
      />
    );
  }
}

IntercomMain.defaultProps = {
};

IntercomMain.propTypes = {
  isVisible: PropTypes.bool.isRequired,
};

const mapStateToProps = state => ({
  isVisible: state.intercom.isVisible,
});

export default connect(mapStateToProps)(IntercomMain);
